import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
import UserMenu from "../../components/Layout/UserMenu";
import axios from "axios";
import { useAuth } from "../../context/auth";
import { useNavigate } from "react-router-dom";

const OrderHistory = () => {
  const [orders, setOrders] = useState([]);
  const [showModal, setShowModal] = useState(false); // State for showing/hiding the modal
  const [review, setReview] = useState(""); // State for the review input field
  const { auth, setAuth } = useAuth();
  const navigate = useNavigate();

  const getOrders = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/orders");
      setOrders(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (auth.token) getOrders();
  }, [auth.token]);

  console.log("Orders:", orders);
  return (
    <Layout title={"Order History"}>
      <div className="container-fluid mt-5">
        <div className="row">
          <div className="col-md-3">
            <UserMenu />
          </div>
          <div className="col-md-9">
            <h2 className="fw-bold text-center opacity">Order History</h2>{" "}
            {orders.map((o, i) => (
              <div className="border shadow mb-4 p-4 rounded" key={o._id}>
                <table className="table">{/* Table headers */}</table>
                <div className="container">
                  {o.products.map((p) => (
                    <div
                      className="row card flex-row mb-3"
                      key={p._id}
                      style={{ borderRadius: "20px" }}
                    >
                      {/* Product image */}
                      {/* Product details */}
                      {/* Review button */}
                      {o.status === "Delivered" && (
                        <button
                          className="btn btn-primary"
                          onClick={() => setShowModal(true)}
                        >
                          Add Review
                        </button>
                      )}
                    </div>
                  ))}
                </div>
                {/* Modal */}
                {showModal && (
                  <div className="modal">
                    <div className="modal-dialog">
                      <div className="modal-content">
                        <div className="modal-header">
                          <h5 className="modal-title">Add Review</h5>
                          <button
                            type="button"
                            className="btn-close"
                            onClick={() => setShowModal(false)}
                          ></button>
                        </div>
                        <div className="modal-body">
                          <input
                            type="text"
                            value={review}
                            onChange={(e) => setReview(e.target.value)}
                            placeholder="Enter your review"
                          />
                        </div>
                        <div className="modal-footer">
                          <button
                            type="button"
                            className="btn btn-primary"
                            // onClick={() => submitReview(o._id, p._id)}
                          >
                            Save Review
                          </button>
                          <button
                            type="button"
                            className="btn btn-secondary"
                            onClick={() => setShowModal(false)}
                          >
                            Cancel
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default OrderHistory;
